import React from "react";
import {
  Button,
  Container,
  Stack,
  Center,
  Slider,
  Progress,
  Loader,
} from "@mantine/core";
import {
  BrandGoogle,
  BrandKickstarter,
  Checks,
  X,
  ChevronRight,
  CircleCheck,
  Router,
} from "tabler-icons-react";
import { SetStateAction, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const TrialPage = () => {
  const [step, setStep] = useState(0);
  const [problemIdx, setProblemIdx] = useState(0);

  const navigate = useNavigate();

  const testDemo = [
    {
      image: "/trial/test1.svg",
      title: "      ?",
      left: "",
      right: "",
    },
    {
      image: "/trial/test2.svg",
      title: "      ?",
      left: "",
      right: "",
    },
    {
      image: "/trial/test3.svg",
      title: "    ?",
      left: "",
      right: "",
    },
    {
      image: "/trial/test4.svg",
      title: "     ?",
      left: "",
      right: "",
    },
  ];

  return (
    <Container className="bg-white h-[100vh]" size={1200}>
      <Stack className="mx-2 h-[100vh]" spacing={0}>
        {step === 0 ? (
          <>
            <Center className="mt-20">
              <img src="/onboarding_logo.svg" height={26} width={86} />
            </Center>
            <Stack className="mt-12">
              <p className="m-0 p-0 font-bold text-xl animate-fadeLessUp text-center">
                  
              </p>
              <p className="p-0 font-bold text-2xl animate-fadeLessUp text-center">
                   
              </p>
            </Stack>
            <Center className="mt-12">
              <img src="/mind_login.svg" width={253} height={176} />
            </Center>

            <Stack className="mt-6" spacing={0}>
              <p className="text-md text-center">
                    
              </p>
              <p className="text-md text-center">  .</p>
            </Stack>
            <Stack className="mt-32">
              <Button
                onClick={() => {
                  setStep(1);
                }}
                rightIcon={<ChevronRight />}
                size="xl"
                radius="md"
                color="violet"
              >
                 
              </Button>
            </Stack>
          </>
        ) : (
          <></>
        )}
        {step === 1 ? (
          <>
            <Stack className="mt-12">
              <p className="p-0 text-xl text-center">   </p>
              <Progress
                value={((problemIdx + 1) * 100) / testDemo.length}
                size="xl"
                color="violet"
                className="mx-6"
              ></Progress>
            </Stack>
            <Center className="mt-12">
              <img
                src={(testDemo[problemIdx] || { image: "" }).image}
                width={240}
                height={168}
              />
            </Center>

            <Stack className="h-24 mt-6" spacing={0}>
              <p className="text-center text-violet-500 text-2xl font-bold">
                Q. {problemIdx + 1}{" "}
              </p>
              <p className="mt-4 text-md text-center font-bold text-lg animate-fadeLessUp">
                {(testDemo[problemIdx] || { title: "" }).title}
              </p>
            </Stack>
            <Slider
              defaultValue={50}
              size="xl"
              className="mt-10"
              step={25}
              showLabelOnHover={false}
              color="violet"
              marks={[
                {
                  value: 0,
                  label: ` ${(testDemo[problemIdx] || { left: "" }).left}`,
                },
                {
                  value: 25,
                  label: ` ${(testDemo[problemIdx] || { left: "" }).left}`,
                },
                { value: 50, label: "" },
                {
                  value: 75,
                  label: ` ${
                    (testDemo[problemIdx] || { right: "" }).right
                  }`,
                },
                {
                  value: 100,
                  label: ` ${
                    (testDemo[problemIdx] || { right: "" }).right
                  }`,
                },
              ]}
            />
            <Stack className="mt-32">
              <Button
                className="shadow-xl"
                onClick={() => {
                  if (problemIdx === testDemo.length - 1) {
                    setStep(2);
                    setTimeout(() => {
                      setStep(3);
                      setTimeout(() => {
                        navigate("/recommend");
                      }, 600);
                    }, 1000);
                  } else setProblemIdx(problemIdx + 1);
                }}
                size="xl"
                radius="md"
                color="violet"
              >
                {problemIdx === testDemo.length - 1 ? "" : ""}
              </Button>
            </Stack>
          </>
        ) : (
          <></>
        )}
        {step >= 2 ? (
          <Stack className="flex  justify-center">
            <Stack className="mt-[380px]">
              <Center>
                <img src="/onboarding_logo.svg" width={145} height={44} />
              </Center>
              <Center>
                {step === 2 ? (
                  <Loader className="my-8" color="violet" size="md" />
                ) : (
                  <CircleCheck size={32} color="purple" />
                )}
              </Center>
              <p className="text-center text-md ">
                   .
              </p>
            </Stack>
          </Stack>
        ) : (
          <></>
        )}
      </Stack>
    </Container>
  );
};

export default TrialPage;
